"use client";
import { useState } from "react";
import axios from "axios";

export default function UploadQR() {
  const [file, setFile] = useState(null);

  const handleUpload = async () => {
    if (!file) return alert("Select a file first");

    const formData = new FormData();
    formData.append("qr_code", file); // ðŸ‘ˆ must match backend

    try {
      const res = await axios.post(
        "/api/products/32/qr-code",      // adjust if full API URL differs
        formData,
        {
          headers: {
            "Content-Type": "multipart/form-data",
            Authorization: `Bearer ${token}`, // ðŸ‘ˆ add real token
          },
        }
      );

      console.log(res.data);
      alert("Uploaded!");
    } catch (err) {
      console.error(err.response?.data || err);
      alert("Upload failed");
    }
  };

  return (
    <div>
      <input
        type="file"
        accept="image/png,image/jpg,image/jpeg"
        onChange={(e) => setFile(e.target.files[0])}
      />

      <button onClick={handleUpload}>Upload QR</button>
    </div>
  );
}
